
import {
	Component,
	ComponentFactory, 
	ReflectiveInjector,
	ViewContainerRef,
	ComponentFactoryResolver,
	Input,
	Output,
	Renderer,
	HostListener,
	EventEmitter,
	Injectable,
	ViewChild,
	ElementRef} from "@angular/core";

import { DragEvent } from 'drag-enabled';

import { PopupLiteService } from '../injectables/popup-lite.service';
import { PopupLiteContentComponent, WindowLiteSelection, PopupLiteOptions, WindowOptions } from '../interfaces/popup-lite.interface';

@Component({
    selector:'popup-lite',
    template: `<div class="popup-lite-overlay" #overlay
	(click)="closeOverlay()"
	[style.display]="config.overlay ? 'block' : 'none'"></div>
<div #modalWondow 
	class="popup-lite" 
	tabindex="0"
	[style.minWidth]="config.minWidth"
	[style.maxWidth]="config.maxWidth"
	[style.display]="config.isOpening ? 'block' : 'none'" 
	[style.position]="config.fixed ? 'fixed':'absolute'"
	[style.top]="config.top.length ? config.top : ''"
	[style.height]="config.height"
	[style.zIndex]="config.zIndex"
	[class.fade-in]="config.isOpen" 
	[class.maximized]="config.maximized"
	[class.pinned]="config.pinned"
	[style.z-index]="config.selected ? 105 : 100"
	(keyup)="keyUp($event)"
	(focus)="selected($event)"
	(click)="selected($event)">
		<div class="controls">
			<a *ngIf="config.pinable"
				class="pin" tabindex="0" 
				(click)="pinModal($event)">
				<span *ngIf="!config.pinned" class="fa fw fa-unlock" aria-hidden="true"></span>
				<span *ngIf="config.pinned" class="fa fw fa-lock" aria-hidden="true"></span>
				<span class="off-screen">Pin</span>
			</a><a *ngIf="config.minimize"
				class="minify" tabindex="0" 
				(click)="minimizeModal($event)" 
				[class.clicked]="config.minimized">
				<span class="fa fw fa-window-minimize" aria-hidden="true"></span>
				<span class="off-screen">Minimize</span>
			</a><a *ngIf="config.maximize"
				class="maxify" tabindex="0" 
				(click)="maximizeModal($event)" 
				[class.clicked]="config.maximized">
				<span class="fa fw fa-window-maximize" aria-hidden="true"></span>
				<span class="off-screen">Maximize</span>
			</a><a *ngIf="config.close"
				class="close" tabindex="0" 
				(click)="onClose($event)">
				<span class="fa fw fa-window-close" aria-hidden="true"></span>
				<span class="off-screen">Close</span>
			</a>
		</div>
		<a *ngIf="config.resizable"
			#resizer
			class="resize-corner" 
			tabindex="0" 
			[medium]="modalWondow"
			[dragInDocument]="resizeEnabled.bind(this)"
			(onDragStart)="onResizeStart($event)"
			(onDrag)="onResizeProgress($event)"
			(onDragEnd)="onResizeEnd($event)">
			<span class="fa fw fa-ellipsis-h" aria-hidden="true"></span>
			<span class="off-screen">Resize</span>
		</a>
		<div *ngIf="config.header"
			#dragHeader
			class="modal-header" 
			[id]="config.id"
			[style.cursor]="(config.dragable && !config.pinned) ? 'all-scroll':'default'"
			[class.pinned]="config.pinned"
			[class.minified]="config.minimized"
			[medium]="modalWondow"
			[dragInDocument]="dragEnabled.bind(this)"
			(onDragStart)="onDragStart($event)"
			(onDrag)="onDrag($event)"
			(onDragEnd)="onDragEnd($event)"
			(dblclick)="maximizeModal($event)">
			<span *ngIf="config.idOnHeader" class="header-title" [textContent]="config.id"></span>
		</div>
		  <div class="modal-body"
		     [class.minimized]="config.minimized"
		     [style.minHeight]="config.minBodyHeight"
			 [style.maxHeight]="config.maxBodyHeight">
			 <ng-template  #content></ng-template>
		  </div>
	      <div class="modal-footer" *ngIf="config.footer"
		  		[class.minimized]="config.minimized">
	         <ng-content select="[modal-footer]"></ng-content>
		  </div>
	    </div>`,
	styles: [`:host .centered{text-align:center;margin:0 auto}:host .popup-lite h2{font-size:.8em;margin:0}:host .popup-lite-overlay{position:absolute;background-color:rgba(44,44,44,.44);width:100%;height:100%;top:0;left:0;z-index:104}:host .popup-lite{-webkit-box-sizing:border-box;box-sizing:border-box;position:absolute;top:100px;left:100px;border-radius:6px;padding:0;z-index:100;background-color:transparent;min-width:300px;-webkit-box-shadow:0 3px 9px rgba(0,0,0,.5);-ms-box-shadow:0 3px 9px rgba(0,0,0,.5);-o-box-shadow:0 3px 9px rgba(0,0,0,.5);box-shadow:0 3px 9px rgba(0,0,0,.5);opacity:0;-webkit-transition:opacity .25s ease-in-out;transition:opacity .25s ease-in-out}:host .popup-lite .off-screen{display:block;float:left;height:0;overflow:hidden;text-indent:-99999px;width:0}:host .popup-lite.fade-in{opacity:1;-webkit-transition:opacity .25s ease-in-out;transition:opacity .25s ease-in-out}:host .popup-lite .controls{position:absolute;top:0;right:2px;border:1px solid #eee;background-color:#fff;border-radius:2px;border-top:0;z-index:2}:host .popup-lite .controls a{text-align:center;border:1px solid #999;-webkit-box-sizing:border-box;box-sizing:border-box;border-radius:0 0 2px 2px;border-top:0;display:inline-block;width:21px;height:21px}:host .popup-lite .controls a span{display:inline-block}:host .popup-lite .controls a.close{cursor:pointer}:host .popup-lite .controls a.close:hover{color:red}:host .popup-lite .controls a.minify{cursor:pointer}:host .popup-lite .controls a.minify.clicked,:host .popup-lite .controls a.minify:hover{color:red}:host .popup-lite .controls a.pin{cursor:pointer}:host .popup-lite .controls a.pin.clicked,:host .popup-lite .controls a.pin:hover{color:red}:host .popup-lite .controls a.maxify{cursor:pointer}:host .popup-lite .controls a.maxify.clicked,:host .popup-lite .controls a.maxify:hover{color:red}:host .popup-lite a{text-align:center;border:1px solid #999;-webkit-box-sizing:border-box;box-sizing:border-box;border-radius:2px}:host .popup-lite a.resize-corner{position:absolute;height:5px;bottom:12px;right:4px;width:13px;border:0;cursor:se-resize}:host .popup-lite a.resize-corner:hover{color:red}:host .popup-lite .modal-header{background-color:#fff;-webkit-box-sizing:border-box;box-sizing:border-box;border-radius:2px 2px 0 0;min-width:100%;min-height:24px;padding:5px 10px}:host .popup-lite .modal-header .header-title{position:absolute;top:0;left:0;padding:2px 5px;-webkit-box-sizing:border-box;box-sizing:border-box;font-size:.9rem}:host .popup-lite .modal-body{background-color:#fff;-webkit-box-sizing:border-box;box-sizing:border-box;padding:10px;overflow-y:auto}:host .popup-lite .modal-footer{background-color:#fff;-webkit-box-sizing:border-box;box-sizing:border-box;border-radius:0 0 2px 2px;min-width:100%;min-height:20px;padding:5px 10px}:host .popup-lite .modal-footer .right{text-align:right}:host .header-off{border-top-left-radius:2px;border-top-right-radius:5px}:host .footer-off{border-bottom-right-radius:5px;border-bottom-left-radius:2px}:host .minimized{padding-top:0!important;padding-bottom:0!important;min-height:0!important}:host .maximized{top:0!important;left:0!important;min-width:100%!important;min-height:100%!important}:host .maximized .modal-footer,:host .maximized .modal-header{width:100%}:host .maximized .modal-body{min-width:100%;min-height:95vh}:host .minimized{min-height:0!important;height:0!important}:host .minified{border-radius:6px!important}:host .popup-lite.maximized{height:inherit!important;min-height:inherit!important}:host .pinned{border:1px dotted red}:host .block-key-events{-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}`]
})
export class PopupLiteComponent {
	private el:HTMLElement;
	private extraclasses = "";
	private selector: WindowLiteSelection;

	@ViewChild("content", {read: ViewContainerRef}) 
	content: ViewContainerRef;

	@ViewChild("modalWondow", {read: ViewContainerRef}) 
	modalWondow: ViewContainerRef;
	
	@ViewChild("resizer", {read: ViewContainerRef}) 
	resizer: ViewContainerRef;
	
	@ViewChild("dragHeader", {read: ViewContainerRef}) 
	dragHeader: ViewContainerRef;
	
	@HostListener('window:resize', ['$event'])
	onResize(event:any) {
		if(this.config.centered && !this.config.pinned){
			let ne = this.el.querySelector('.popup-lite');
			let root: HTMLElement = this.el.parentElement;
            this.renderer.setElementStyle(ne, 'left', ((root.getBoundingClientRect().width-ne.getBoundingClientRect().width)/2) + "px");
		}
	}

	config: WindowOptions ={
		id:'',
		close: false,
		overlay: false,
		closeOnOverlay: false,
		minimize: false,
		maximize: false,
		dragable:false,
		resizable:false,
		centered: false,
		fixed: false,
		pinable:false,

		height:'',
		width:'',
		maxBodyHeight:'',
		minBodyHeight:'',
		minWidth:'',
		maxWidth:'',
		adjustHeight:false,
		isOpen: false,
		isOpening:false,
		minimized:false,
		maximized:false,
		pinned:false,
		zIndex:100,
		top: ''
	}

	constructor(
		el: ElementRef, 
		private componentFactoryResolver: ComponentFactoryResolver,
		private renderer:Renderer) {
		this.el = el.nativeElement;
    }

	private calcMaxHeight(node:any, target:string){
		let list = node.childNodes;
		let max = 0;

		for (let i = 0; i < list.length; i++) {
		   if(list[i].nodeName.toLowerCase()===target){
			   list = list[i].childNodes;
			   for (let i = 0; i < list.length; i++) {
				   if(list[i].nodeType===1){
					   max += (list[i].clientHeight+list[i].offsetHeight);
					}
				}
				break;
		   }
        }
    	return max;
	}

	init(component, data, config: PopupLiteOptions, selector: WindowLiteSelection) {
		let componentFactory = this.componentFactoryResolver.resolveComponentFactory(component);
		let componentRef = this.content.createComponent(componentFactory);
		(<PopupLiteContentComponent>componentRef.instance).data = data;
		(<PopupLiteContentComponent>componentRef.instance).id = config.id;
		
		if (config) {
			const list = Object.keys(config);
			list.map((key) => {
				this.config[key] = config[key];
			})
		}
		this.selector = selector;

		this.display(config);
	}
		
	public display(props:WindowOptions){
		this.config.maxBodyHeight = props && props.maxHeight ? props.maxHeight:'';
		 this.config.minWidth = props && props.minWidth ? props.minWidth:'';
		 this.config.maxWidth = props && props.maxWidth ? props.maxWidth:'';
		 this.config.top = props && props.top ? props.top : '';
		 this.config.isOpening = true;
		 this.config.adjustHeight = props && props.adjustHeight ? props.adjustHeight : false;
		 this.extraclasses = this.config.header ? "":"header-off ";
		 this.extraclasses += this.config.footer ? "":"footer-off ";
		setTimeout(function() {
			this.onResize(null);
			this.config.isOpen = true;
		}.bind(this),10);
		return false;
	}

	keyUp(event) {
		event.preventDefault();
		const code = event.which;

		if (code === 13) {
			event.target.click();
		}		
	}
	closeOverlay(){
		if (this.config.closeOnOverlay) {
		this.closeModal(null, { id: this.config.id, confirmed: false });
		}
	}
	private onClose(event) {
		this.closeModal(event, { id: this.config.id, confirmed: false });
	}
	private closeModal($event:any, result){
		this.config.isOpening = false;
		this.config.overlay = false;
		this.config.isOpen = false;
		this.selector.popedOut(this.config.id, result);

		return false;
	}
	private minimizeModal($event:any){
		this.config.minimized = !this.config.minimized;
		if(this.config.resizable){
		  let ne:any = this.el.querySelector('.resize-corner');
		  let wn:any = this.el.querySelector('.popup-lite');
		  let bd:any = this.el.querySelector('.modal-body');
		  if(!this.config.minimized){bd.style.height=bd.getAttribute("oh");bd.style.maxHeight= "inherit";}
		  else {
		  bd.style.height = "0";
		  wn.style.height="inherit"
		  }
		  ne.style.display= (this.config.minimized || this.config.maximized) ? 'none':'block';
		}
		return false;
	}
	private maximizeModal($event:any){
		this.config.maximized = !this.config.maximized;
		if(this.config.resizable){
		  let ne:any = this.el.querySelector('.resize-corner');
		  let bd:any = this.el.querySelector('.modal-body');
		  if(bd.getAttribute("oh")){bd.style.height=bd.getAttribute("oh");}
		  ne.style.display= (this.config.minimized || this.config.maximized) ? 'none':'block';
		}
		return false;
	}
	selected($event: any){
		this.selector.setSelected(this.config.id);
		return true;
	}
	private pinModal($event:any){
		this.config.pinned = !this.config.pinned;
		return false;
	}

	dragEnabled(event: DragEvent) {
		return this.config.dragable && !this.config.pinned;
	}
	onDragStart(event: DragEvent){
	}
	onDrag(event: DragEvent){
		if(event.node === this.dragHeader.element.nativeElement) {
			this.renderer.setElementStyle(event.medium, 'left', (event.clientX-event.offset.x)+"px");
			this.renderer.setElementStyle(event.medium, 'top', (event.clientY-event.offset.y)+"px");
		}
	}
	onDragEnd(event: DragEvent){
		if(event.node === this.dragHeader.element.nativeElement) {
			this.renderer.setElementStyle(event.medium, 'left', (event.clientX-event.offset.x)+"px");
			this.renderer.setElementStyle(event.medium, 'top', (event.clientY-event.offset.y)+"px");
		}
	}

	resizeEnabled(event: DragEvent) {
		return this.config.resizable;
	}
	onResizeStart(event: DragEvent){
	}
	onResizeProgress(event: DragEvent){
		if(event.node === this.resizer.element.nativeElement) {
			const wr = event.medium.getBoundingClientRect();
			const width =  (event.clientX-event.offset.x) - wr.left;
			const height = (event.clientY-event.offset.y) - wr.top;
			let hd = this.el.querySelector('.modal-header');
			let ft = this.el.querySelector('.modal-footer');
			let bd = this.el.querySelector('.modal-body');
			let fth= ft.getBoundingClientRect().height;
			let hdh= hd.getBoundingClientRect().height;
			let h = height - hdh - fth -2;

			if(width>200 && height>60){
				this.renderer.setElementStyle(event.medium, 'width', width+"px");
				this.renderer.setElementStyle(event.medium, 'height', height+"px");
				this.renderer.setElementStyle(bd, 'height', h+"px");
			}
		}
	}
	onResizeEnd(event: DragEvent){
		if(event.node === this.resizer.element.nativeElement) {
			const wr = event.medium.getBoundingClientRect();
			const width =  (event.clientX-event.offset.x) - wr.left;
			const height = (event.clientY-event.offset.y) - wr.top;

			if(width>200 && height>60){
				let hd = this.el.querySelector('.modal-header');
				let ft = this.el.querySelector('.modal-footer');
				let bd = this.el.querySelector('.modal-body');
				let fth= ft.getBoundingClientRect().height;
				let hdh= hd.getBoundingClientRect().height;
				let h = height - hdh - fth -2;
					
				this.renderer.setElementStyle(event.medium, 'width', width+"px");
				this.renderer.setElementStyle(event.medium, 'height', height+"px");
				this.renderer.setElementStyle(bd, 'height', h+"px");
			}
		}
	}
}
